import { styled } from "styled-components";

export const BoxWrapper = styled.div`
  position: relative;
  .single-actuality {
    &::after {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #000;
      opacity: 0.4;
      //lighten-color(#007bff, 10%);
    }
  }
`;
export const Inner = styled.div`
  position: absolute;
  width: 100%;
  padding: 20px;
  bottom: 0;
  z-index: 2;
`;
