import { styled } from "styled-components";

export const SectionHolderBlog = styled.section`
  padding: 40px 0;
  .title {
    text-align: center;
    text-transform: uppercase;
    font-weight: 800;
  }
`;
export const SectionInner = styled.div`
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
`;
