import React, { useState } from "react";
import "./actualites.scss";
import { Wrapper, Inner, Rows } from "./ActualitesSection.style";
import { Link, graphql, useStaticQuery } from "gatsby";
import BoxOverlay from "../../molecules/BoxOverlay/BoxOverlay";

const ActualitesSection = () => {
  const actus = useStaticQuery(graphql`
    query MyQuery {
      actualites: allMarkdownRemark(filter: { fileAbsolutePath: { regex: "/content/actualites/" } }) {
        edges {
          node {
            id
            frontmatter {
              id
              title
              Image {
                childImageSharp {
                  gatsbyImageData(blurredOptions: { width: 200 }, placeholder: BLURRED, transformOptions: { cropFocus: CENTER }, width: 1000)
                }
              }
            }
          }
        }
      }
    }
  `);
  return (
    <Wrapper>
      <Inner>
        <h1 className="actuality-title">
          ACTUALITS DE <strong>L'ASSOCIATION</strong>
        </h1>
        <Rows className="actuality-rows">
          {actus.actualites.edges.map((node, index) => {
            return <BoxOverlay key={node.node.id} title={node.node.frontmatter.title} imageUrl={node.node.frontmatter.Image} id={node.node.frontmatter.id} />;
          })}
        </Rows>
        <Link className="btn-more-actualities" to="/nos-actualites/">
          voir toutes les actualits de l'association
        </Link>
      </Inner>
    </Wrapper>
  );
};

export default ActualitesSection;
