import React from "react";
import { DirectWrapper, Inner } from "./DirectBoxStyle";
import "./DirectBox.scss";
import { Link } from "gatsby";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay } from "@fortawesome/free-solid-svg-icons";
import Bg from "../../../../images/bg.jpg";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
const DirectBox = ({ title, imageUrl, index }) => {
  return (
    <DirectWrapper className="directHolder child">
      <Link to={`/nos-missions-realisees-et-direct-du-terrain/missions/${index}`} className="single-direct">
        <GatsbyImage imgClassName="actu-img" image={getImage(imageUrl)} alt={imageUrl} />
        <Inner className="boxInner">
          <p>{title}</p>
          <div className="call-action">
            Lire <FontAwesomeIcon icon={faPlay} />
          </div>
        </Inner>
      </Link>
    </DirectWrapper>
  );
};

export default DirectBox;
