import React from "react";
import { BoxWrapper, Inner } from "./BoxOverlayStyle";
import "./BoxOverlay.scss";
import { Link } from "gatsby";
import { GatsbyImage, StaticImage, getImage } from "gatsby-plugin-image";

const BoxOverlay = ({ title, imageUrl, id }) => {
  return (
    <BoxWrapper className="boxWrapper">
      <Link to={`/nos-actualites/actualites/${id}`} className="single-actuality">
        <GatsbyImage imgClassName="actu-img" image={getImage(imageUrl)} alt={imageUrl} />
        <Inner className="boxInner">{title}</Inner>
      </Link>
    </BoxWrapper>
  );
};

export default BoxOverlay;
