import React from "react";
import { BoxWrapper, BoxInner, BoxBody, BoxFooter, BoxHeader } from "./BoxDon.style";
import "./BoxDon.scss";
import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
const BoxDon = ({ imageUrl, title, price }) => {
  return (
    <Link to={`/je-fais-un-don/${price}`}>
      <BoxWrapper>
        <BoxInner className="box-don-inner">
          <BoxHeader className="box-don-header">
            <div className="box-don-price">
              <span className="price-text">{price}</span>
              <span className="price-sigle">FCFA</span>
            </div>
          </BoxHeader>
          <BoxBody className="don-body">
            <p className="don-desc">{title}</p>
            <GatsbyImage imgClassName="actu-img" image={getImage(imageUrl)} alt={imageUrl} />
          </BoxBody>
          <BoxFooter className="call-action-don-btn">
            <Link>je donne</Link>
          </BoxFooter>
        </BoxInner>
      </BoxWrapper>
    </Link>
  );
};

export default BoxDon;
