import React from "react";
import "./rejoignez-nous.scss";
import { Wrapper, Inner, Image, Content, Title } from "./RejoignezNousSection.style.js";
import { StaticImage } from "gatsby-plugin-image";
import { Link } from "gatsby";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay } from "@fortawesome/free-solid-svg-icons";
const RejoignezNousSection = () => {
  return (
    <Wrapper>
      <Inner className="innerHolder">
        <Image>
          <StaticImage imgClassName="img-agissez" width={2000} height={2000} className="img-fluid" src="../../../images/people.jpg" loading="eager" />
        </Image>
        <Content className="contentHolder">
          <div className="content">
            <Title className="p-mb">AGISSEZ AVEC LADIES JOY REJOIGNEZ-NOUS</Title>
            <p>
              Les femmes et jeunes filles ont besoin de dfenseurs. Engagez-vous, faites-vous entendre, devenez bnvole ou donateur et offrez  chaque femme une nouvelle images dmancipation et
              dintgration sociale.
            </p>

            <Link to="/je-deviens-adherent/" className="call-action">
              <FontAwesomeIcon icon={faPlay} />
              <span>Rejoindre maintenant</span>
            </Link>
          </div>
        </Content>
      </Inner>
    </Wrapper>
  );
};

export default RejoignezNousSection;
