import React from "react";
import { useState } from "react";
import "./actions.style.scss";
import { Link } from "gatsby";
const CardAction = ({ content }) => {
  return (
    <div className="sectionAction-card">
      <p className="action-text">{content}</p>
    </div>
  );
};
const SectionActions = ({ btnText, link, extraClassName = "", actions }) => {
  /* const [actions, setActions] = useState(actions); */
  return (
    <div className="sectionActions">
      <div className="container">
        <div className="sectionsActions-content">
          <div className="sectionActions-heading">
            <h1 className={`${extraClassName ? extraClassName + " title" : "title"}`}>le travail de ladie's joy</h1>
            <p className="desc">Dcouvrez les actions de LADEIS JOY pour lautonomisation de la femmes et la dfense des droits et valeurs des jeunes filles.</p>
          </div>
          <div className="sectionActions-cards">
            {actions.map((item, index) => (
              <CardAction key={index} content={item.content} />
            ))}
          </div>
        </div>
        <Link to={link} className="to-call-action">
          {btnText}
        </Link>
      </div>
    </div>
  );
};

export default SectionActions;
