const BlogSection = () => {
  const actus = useStaticQuery(graphql`
    query PostsQuery {
      posts: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "/content/blogContent/" } }
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              description
              Image {
                childImageSharp {
                  gatsbyImageData(
                    blurredOptions: { width: 200 }
                    placeholder: BLURRED
                    transformOptions: { cropFocus: CENTER }
                    width: 1000
                  )
                }
              }
            }
          }
        }
      }
    }
  `);
  const [posts, setPosts] = useState(actus.posts.edges);
  return (
    <SectionHolderBlog>
      <div className="container">
        <h1 className="title">
          LADIES JOY, pour un changement <br /> de destin fminin
        </h1>
        <SectionInner>
          {posts.map((item, index) => (
            <Post {...item.node.frontmatter} />
          ))}
        </SectionInner>
      </div>
    </SectionHolderBlog>
  );
};

export default BlogSection;
