import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "./direct.style.scss";
import { SectionHolder, SectionInner, Rows } from "./sectionDirect.style";

import { Link, graphql, useStaticQuery } from "gatsby";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import DirectBox from "./DirectBox/DirectBox";
const DirectGrid = ({ directs }) => {
  return (
    <Rows className="section-directs-rows">
      {directs.map((direct, index) => {
        return <DirectBox key={index} index={direct.id} title={direct.title} imageUrl={direct.ImageSource} />;
      })}
    </Rows>
  );
};
const SectionDirect = ({ directs }) => {
  return (
    <SectionHolder className="section-directs">
      <div className="wrapper">
        <SectionInner>
          <h1>
            EN DIRECT DU <strong>TERRAIN</strong>
          </h1>
          <Swiper className="swiperHolder container" navigation={true} modules={[Navigation]}>
            <div className="">
              <SwiperSlide>
                <DirectGrid directs={directs} />
              </SwiperSlide>
            </div>
            <div className="">
              <SwiperSlide>
                <DirectGrid directs={directs} />
              </SwiperSlide>
            </div>
          </Swiper>
          <Link to="/nos-missions-realisees-et-direct-du-terrain" className="call-action-view-more-direct-btn ">
            Voir <FontAwesomeIcon icon={faPlus} />
          </Link>
        </SectionInner>
      </div>
    </SectionHolder>
  );
};

export default SectionDirect;
