import React from "react";
import "./DonsSection.scss";
import { Wrapper, Rows } from "./DonsSection.style";
import BoxDon from "./BoxDon/BoxDon";
import { graphql, useStaticQuery } from "gatsby";
export const DonsSections = () => {
  const actus = useStaticQuery(graphql`
    query MyQueryForDons {
      dons: allMarkdownRemark(
        filter: { fileAbsolutePath: { regex: "/content/dons/" } }
      ) {
        edges {
          node {
            id
            frontmatter {
              title
              price
              Image {
                childImageSharp {
                  gatsbyImageData(
                    height: 250
                    blurredOptions: { width: 200 }
                    placeholder: BLURRED
                    transformOptions: { cropFocus: CENTER }
                    aspectRatio: 1.5
                  )
                }
              }
            }
          }
        }
      }
    }
  `);
  return (
    <Wrapper className="donsHolder-section">
      <div className="container">
        <div className="dons-section-inner">
          <h1>
            VOS DONS <strong>TRANSFORME DES VIES</strong>
          </h1>
          <Rows className="dons-rows">
            {actus.dons.edges.map((node) => {
              return (
                <BoxDon
                  price={node.node.frontmatter.price}
                  title={node.node.frontmatter.title}
                  imageUrl={node.node.frontmatter.Image}
                />
              );
            })}
          </Rows>
        </div>
      </div>
    </Wrapper>
  );
};
